import React from "react";
import { Grid, Paper, Typography, Button } from "@mui/material";

const Displayer = ({ currentPair, handlePreference }) => (
  <>
    <Grid container spacing={3}>
      {[0, 1].map((index) => (
        <Grid item xs={12} sm={6} key={index}>
          <Paper
            elevation={3}
            style={{
              padding: "20px",
              backgroundColor:
                currentPair.preference === `Vereinfachung ${index + 1}`
                  ? "#4caf50"
                  : "#666",
            }}
          >
            <Typography
              variant="body1"
              color="white"
              gutterBottom
              sx={{ textAlign: "justify" }}
            >
              Vereinfachung {index + 1}:
              <br />
              <br />
              {currentPair.simplifications[index]}
              <br />
            </Typography>
            <Button
              variant="contained"
              color="success"
              onClick={() => handlePreference(`Vereinfachung ${index + 1}`)}
            >
              Dieser Text ist besser
            </Button>
          </Paper>
        </Grid>
      ))}
    </Grid>
  </>
);

export default Displayer;
