import React from "react";
import { Grid, Box, Typography, Button } from "@mui/material";

const Submission = ({
  currentPairIndex,
  textPairs,
  currentPair,
  handleSubmit,
}) =>
  currentPairIndex === textPairs.length - 1 &&
  currentPair.preference && (
    <Grid item xs={12} sm={12}>
      <Box textAlign="center" style={{ marginBottom: "10px" }}>
        <Typography
          variant="h5"
          component="p"
          color="white"
          sx={{
            backgroundColor: "#666",
            borderRadius: "5px",
            width: "100%",
            margin: "20px auto",
          }}
        >
          Jetzt knnen Sie Ihre Antworten abschicken! Vielen Dank!
        </Typography>
        <Button
          variant="contained"
          color="primary"
          onClick={handleSubmit}
          disabled={textPairs.length === 0 || !currentPair.preference}
        >
          Absenden
        </Button>
      </Box>
    </Grid>
  );

export default Submission;
