const Dataloader = ({
  userID,
  loadCSV,
  isDatasetLoaded,
  progress,
  currentPairIndex,
  textPairs,
  handleNext,
  handlePrevious,
}) => {
  return (
    <Box my={4} textAlign="center">
      <Typography
        variant="h4"
        component="h1"
        gutterBottom
        color="white"
        sx={{
          backgroundColor: "#666",
          padding: "10px",
          fontSize: "1.5rem",
          borderRadius: "4px",
          display: "inline-block",
          marginBottom: "30px",
        }}
      >
        Herzlich Willkommen bei unserer Studie!
      </Typography>

      {
        <Box mb={4}>
          <Typography
            variant="h6"
            gutterBottom
            color="white"
            sx={{
              backgroundColor: "#666",
              padding: "10px",
              borderRadius: "4px",
              display: "inline-block",
            }}
          >
            Am Morgen machen Sie Datensatz A und B. <br /> Am Nachmittag machen
            Sie Datensatz C und D. <br /> Es gibt eine 10-Minuten Pause zwischen
            durch. <br /> Sarah Ebling bedankt sich fr Ihre Teilnahme!
          </Typography>
          <Box mt={2}>
            {["A", "B", "C", "D"].map((letter) => (
              <Button
                key={letter}
                variant="contained"
                color="primary"
                onClick={() =>
                  loadCSV(getAnnoDatasetPath(userID) + letter + `.csv`)
                }
                style={{ margin: "10px" }}
                disabled={isDatasetLoaded}
              >
                Datensatz {letter}
              </Button>
            ))}
          </Box>
        </Box>
      }

      {textPairs.length > 0 && (
        <Box mt={4}>
          <LinearProgress
            variant="determinate"
            value={progress}
            sx={{
              height: 25,
              backgroundColor: "#666",
              "& .MuiLinearProgress-bar": { backgroundColor: "#4caf50" },
            }}
          />
          <Box textAlign="center" alignItems="center" mt={1}>
            <Typography
              variant="body1"
              color="white"
              sx={{ backgroundColor: "#666" }}
            >
              Noch {textPairs.length - currentPairIndex - 1}{" "}
              {textPairs.length - currentPairIndex - 1 === 1 ? "Text" : "Texte"}{" "}
              brig
            </Typography>
          </Box>
          <Box
            display="flex"
            justifyContent="space-between"
            textAlign="center"
            mt={4}
          >
            <Button
              variant="contained"
              color="primary"
              onClick={handlePrevious}
              disabled={currentPairIndex === 0}
            >
              Zurck
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={handleNext}
              disabled={currentPairIndex === textPairs.length - 1}
            >
              Weiter
            </Button>
          </Box>
        </Box>
      )}
    </Box>
  );
};

export default Dataloader;
